@import url(https://fonts.googleapis.com/css2?family=Arvo&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Arvo&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
    text-align: center;
}

#homeIMG{
    width: 100%;
    margin-top: -100px;
    z-index: 1;
}
.header{
    height: 50px;
    background: rgb(206, 253, 255);
    font-family:  'Arvo';
    font-size: 30px;
    display:flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
}

a{
    font-size: 15px;
}

.product{
    display: flex;
    justify-content: center;
    align-items: stretch;
}

.carousel{
    width: 250px !important;
    margin: 30px;
}

.description{
    margin-top: 30px;
}

.info{
    width: 30%;
}
.Header{
    display: flex;
    height: 100px;
    font-family: 'Arvo';
    font-size: 50px;
    padding: 0px 20px;
    font-stretch: expanded;
    text-shadow: -2px 0 white, 0 2px white, 2px 0 white, 0 -2px white;
    align-items: center;
    justify-content: space-between;
    color: black;
    z-index: 2;
    position: relative;
}

#right{
    font-size: 20px;
    display: flex;
    margin-right: 20px;
}

a{
    padding: 10px;
    text-decoration: inherit;
}


#title{
    font-size: 35px;
    padding: 30px;
}

.btnWhatsapp{
    width: 200px;
    height: 40px;
    background: rgb(0, 255, 0);
    border-radius: 5px;
    color:white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-decoration: inherit;
    margin: 20px auto;
}

.products{
    display: flex;
    justify-content: center;
}


@media (max-width: 660px){
    #right{
        display: none;
    }
    .Header{
        font-size: 35px;
        display: flex;
        justify-content: center;
    }   
}
.card{
    width: 250px;
    min-width: 250px;
    border-radius: 5px;
    box-shadow: 5px 5px 5px 2px rgba(0, 0, 0, 0.194);
    margin: 25px;
    border: 1px solid rgba(0, 0, 0, 0.167);
    background-color: rgb(237, 237, 237);
    text-align: center;
}

#img{
    width: 100%;
    height: 90%;
}

.title{
    font: 500;
    font-size: 20px;
    padding: 20px;
}

.text{
    padding: 40px;
}

.link{
    margin-bottom: 20px !important;
}

a{
    color: inherit;
    text-decoration: inherit;
}

#linkArea{
    padding: 0;
}

